export default "import React from 'react';\nimport Area from '@splunk/dashboard-visualizations/Area';\n\nexport default () => (\n    <Area\n        options={{ 'axisLabelsX.majorLabelVisibility': 'hide' }}\n        encoding={{\n            x: 'primary.component',\n            y: 'primary.count',\n        }}\n        dataSources={{\n            primary: {\n                requestParams: { offset: 0, count: 20 },\n                data: {\n                    fields: [{ name: 'component', groupby_rank: '0' }, { name: 'count' }],\n                    columns: [\n                        ['ApplicationLicense', 'DatabaseDirectoryManager', 'IndexWriter'],\n                        ['166238', '8904', '2327'],\n                    ],\n                },\n                meta: { totalCount: 20 },\n            },\n        }}\n    />\n);\n";