export default "import React from 'react';\nimport Area from '@splunk/dashboard-visualizations/Area';\n\nexport default () => (\n    <Area\n        options={{\n            'axisY2.enabled': true,\n            'chart.overlayFields': 'count',\n            'axisTitleY2.text': 'Axis - Y2 Count',\n            'axisY2.abbreviation': 'auto',\n        }}\n        encoding={{\n            x: 'primary.sourcetype',\n            y: 'primary.percent',\n            y2: 'primary.count',\n        }}\n        dataSources={{\n            primary: {\n                requestParams: { offset: 0, count: 20 },\n                data: {\n                    fields: [\n                        {\n                            name: 'sourcetype',\n                        },\n                        {\n                            name: 'count',\n                            type_special: 'count',\n                        },\n                        {\n                            name: 'percent',\n                            type_special: 'percent',\n                        },\n                    ],\n                    columns: [\n                        [\n                            'splunkd',\n                            'splunkd_ui_access',\n                            'splunkd_access',\n                            'splunk_web_access',\n                            'scheduler',\n                            'splunk_web_service',\n                        ],\n                        ['1600', '1525', '1295', '713', '322', '19'],\n                        ['87.966380', '50.381304', '60.023780', '121.183272', '70.250513', '90.194752'],\n                    ],\n                },\n                meta: { totalCount: 20 },\n            },\n        }}\n    />\n);\n";