export default "import React from 'react';\nimport Area from '@splunk/dashboard-visualizations/Area';\n\nexport default () => (\n    <Area\n        options={{\n            'axisTitleY.text': 'Axis Y Count',\n            'axisTitleX.text': 'Axis X Time',\n        }}\n        encoding={{\n            x: 'primary._time',\n            y: 'primary[1]',\n        }}\n        dataSources={{\n            primary: {\n                requestParams: { offset: 0, count: 20 },\n                data: {\n                    fields: [{ name: '_time', groupby_rank: '0' }, { name: 'count' }, { name: '_span' }],\n                    columns: [\n                        [\n                            '2018-05-02T18:15:46.000-07:00',\n                            '2018-05-02T18:15:47.000-07:00',\n                            '2018-05-02T18:15:48.000-07:00',\n                            '2018-05-02T18:15:49.000-07:00',\n                            '2018-05-02T18:15:50.000-07:00',\n                        ],\n                        ['2', '10', '13', '60', '43'],\n                        ['1', '1', '1', '1', '1'],\n                    ],\n                },\n                meta: { totalCount: 20 },\n            },\n        }}\n    />\n);\n";